@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700|Roboto+Mono:300&amp;display=swap');
 
body {
  font-family: 'Roboto', sans-serif!important; 
}
.bg-primary, .jumbotron { 
  background-image: -webkit-linear-gradient(left, #720e9e 0, #dd4b39 100%);
  background-image: -o-linear-gradient(left, #720e9e 0, #dd4b39 100%);
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #720e9e), to(#dd4b39));
  background-image: linear-gradient(to right, #720e9e 0, #dd4b39 100%);
  -webkit-box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
          box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
}
.jumbotron {
  position: relative;
  font-size: 16px;
  color: #fff;
  color: rgba(255,255,255,0.75);
  text-align: center;
  border-radius: 0;
  padding-bottom: 120px;
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff720e9e', endColorstr='#ffdd4b39', GradientType=1);
}
 .jumbotron .bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding:5px 15px;
    background-color: rgba(0,0,0,0.2);
  }
.jumbotron .bottom div:first-child { line-height: 30px;}
.jumbotron p{
  font-size: 18px;
  font-weight: 300;
}
.navbar-brand i{
  vertical-align: sub;
  margin-right: 10px;
  border: 1px solid #ad03f7;
  padding: 2px 2px;
  border-radius: 4px;
  -webkit-box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
    box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
}
.nav-link {  padding: .5rem 0rem;}
.nav-link svg{
  height: 20px;
  color: #fff;
  vertical-align: sub;
}
.search_icon{
  height: 40px;
  width: 40px;
  float: right;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 50%;
  color: #d54741;
  }
.search-field { 
  position: relative;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  border-radius: 8px;
  border: 1px solid #ce4247;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: 24px;
  z-index: 3;
  height: 44px;
  margin: 0 auto;
  width: 482px;}
  .search-field .form-control,  .search-field .form-control:focus{    
    background: transparent;
    border: none;
    outline: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .search-field:hover{
    -webkit-box-shadow: 1px 2px 14px 7px rgba(32,33,36,0.28);
            box-shadow: 1px 2px 14px 7px rgba(32,33,36,0.28);
    border: 1px solid #ce4247;
  }
  .search_icon{   
    position: absolute;
    top: 0;
    right: 0;
  }
  .genre-search select.form-control:not([size]):not([multiple]) {
    padding: 0 5px;
    height: 29px;
    background-color: #dc4b3a;
    border-color: #9e3140;
    color: #fff;
}
.genre-title{ 
  color: #777;
}
.card-body h4 {
  font-size: 30px;
  color: rgba(0,0,0,.87);
  font-weight: normal;
  margin-bottom: 0;
}
.card-text,.genre-title{
  font-size: small;
}
ul.list {
  list-style: none;
  margin-top: 0;
  padding: 0;
} 
ul.list li {
  text-align: left;
  font-size: 12px;
}
.shadow-sm text { display: none;}


